.nav {
  height: 96px;
  width: 100%;
  position: sticky;
  z-index: 10;
  top: 0;
  display: flex;
  align-items: center;
  padding-block: 26px 8px;
  padding-inline: 4%;
  background-color: white;
}
.nav .logo {
  height: 100%;
}
.nav .logo img {
  height: 100%;
}
.nav .navbar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: start;
  list-style: none;
  margin-inline-start: 8%;
}
.nav .navbar li {
  margin-inline: 3%;
  font-size: 18px;
  font-weight: 500;
}
.nav .navbar li a {
  cursor: pointer;
  color: var(--text-color);
  white-space: nowrap;
  transition: 0.3s ease-in;
  padding-bottom: 8px;
  border-bottom: 3px solid rgba(255, 255, 255, 0);
}
.nav .navbar li a:hover {
  color: var(--main-color);
  padding-bottom: 8px;
  border-bottom: 3px solid var(--main-color);
}
.nav .navbar li .active {
  color: var(--main-color);
  border-bottom: 2px solid var(--main-color);
}
.nav .toggle-navbar {
  margin-inline-start: auto;
}
.nav .toggle-navbar .burger-wrapper {
  display: none;
  cursor: pointer;
  margin: auto;
  width: 50px;
  height: 40px;
}
.nav .toggle-navbar .burger-wrapper .hamburger {
  background: black;
  width: 50px;
  height: 5px;
  position: relative;
  transition: background 10ms 300ms ease;
  transform: translateY(20px);
}
.nav .toggle-navbar .burger-wrapper .hamburger::before, .nav .toggle-navbar .burger-wrapper .hamburger::after {
  transition: top 300ms 350ms ease, transform 300ms 50ms ease;
  position: absolute;
  background: black;
  width: 50px;
  height: 5px;
  content: "";
}
.nav .toggle-navbar .burger-wrapper .hamburger::before {
  top: -15px;
}
.nav .toggle-navbar .burger-wrapper .hamburger::after {
  top: 15px;
}
.nav .toggle-navbar .menu-trigger:checked ~ .burger-wrapper .hamburger {
  background: transparent;
}
.nav .toggle-navbar .menu-trigger:checked ~ .burger-wrapper .hamburger::before, .nav .toggle-navbar .menu-trigger:checked ~ .burger-wrapper .hamburger::after {
  transition: top 300ms 50ms ease, transform 300ms 350ms ease;
  top: 0;
}
.nav .toggle-navbar .menu-trigger:checked ~ .burger-wrapper .hamburger::before {
  transform: rotate(45deg);
}
.nav .toggle-navbar .menu-trigger:checked ~ .burger-wrapper .hamburger::after {
  transform: rotate(-45deg);
}
.nav .toggle-navbar .hidden {
  display: none;
}

@media screen and (max-width: 900px) {
  .nav .navbar {
    flex-direction: column;
    position: absolute;
    top: 96px;
    height: 100vh;
    width: 80%;
    max-width: 400px;
    gap: 7%;
    margin: 0;
    padding: 24px;
    inset-inline-start: 0;
    background-color: var(--background-color);
    box-shadow: 0px 8px 8px 1px rgba(65, 65, 65, 0.2);
  }
  .nav .toggle-navbar {
    display: flex;
  }
  .nav .show-navbar {
    inset-inline-start: 0;
    transition: 0.6s;
  }
  .nav .hide-navbar {
    inset-inline-start: -100%;
    transition: 0.6s;
  }
  .nav .toggle-navbar .burger-wrapper {
    display: flex;
    margin-bottom: 12px;
  }
}/*# sourceMappingURL=NavbarStyle.css.map */