.nav {
    height: 96px;
    width: 100%;
    position: sticky;
    z-index: 10;
    top: 0;
    display: flex;
    align-items: center;
    padding-block: 26px 8px;
    padding-inline: 4%;
    background-color: white;
    .logo {
        height: 100%;
        img {
            height: 100%;
        }
    }

    .navbar {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: start;
        list-style: none;
        margin-inline-start:8% ;

        li {

            margin-inline:3% ;
            font-size: 18px;
            font-weight: 500;

            a {
                cursor: pointer;
                color: var(--text-color);
                white-space: nowrap;
                transition: 0.3s ease-in;
                padding-bottom: 8px;
                border-bottom: 3px solid rgba(255, 255, 255, 0);
                &:hover{
                    color: var(--main-color);
                    padding-bottom: 8px;
                    border-bottom: 3px solid var(--main-color);
                }
            }

            .active {
                color: var(--main-color);
                border-bottom: 2px solid var(--main-color);
            }
        }
    }
    .toggle-navbar{
        margin-inline-start:auto ;
        .burger-wrapper {
            display: none;
            cursor: pointer;
            margin: auto;
            width: 50px;
            height: 40px;
          
            .hamburger {
              background: black;
              width: 50px;
              height: 5px;
              position: relative;
              transition: background 10ms 300ms ease;
              transform: translateY(20px);
          
              &::before,
              &::after {
                transition: top 300ms 350ms ease, transform 300ms 50ms ease;
                position: absolute;
                background: black;
                width: 50px;
                height: 5px;
                content: "";
              }
          
              &::before {
                top: -15px;
              }
          
              &::after {
                top: 15px;
              }
            }
          }
          
          .menu-trigger:checked ~ .burger-wrapper {
            .hamburger {
              background: transparent;
          
              &::before,
              &::after {
                transition: top 300ms 50ms ease, transform 300ms 350ms ease;
                top: 0;
              }
          
              &::before {
                transform: rotate(45deg);
              }
          
              &::after {
                transform: rotate(-45deg);
              }
            }
          }
          
          .hidden {
            display: none;
          }
    }

}
@media screen and (max-width:900px) {
    .nav{
        .navbar{
            flex-direction: column;
            position: absolute;
            top: 96px;
            height: 100vh;
            width: 80%;
            max-width: 400px;
            gap: 7%;
            margin: 0;
            padding: 24px;
            inset-inline-start: 0;
            background-color: var(--background-color);
            box-shadow: 0px 8px 8px 1px #41414133;

        }
        .toggle-navbar{
            display: flex;
        }
        .show-navbar{
            inset-inline-start: 0;
            transition: 0.6s;
        }
        .hide-navbar{
            inset-inline-start: -100%;
            transition: 0.6s;

        }
        .toggle-navbar{
            .burger-wrapper{
            display: flex;
            margin-bottom: 12px;
        }
        }
        
    }
}

  